/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.blog {
  padding: 10px;
  font-family: 'Arial', sans-serif;
  color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.blogcontainer {
  width: 100%;
  max-width: 1200px;
  padding: 20px;
  border-radius: 10px;
}

.header {
  text-align: center;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.title {
  font-size: 1rem;
  color: #7f13f9;
}

.subtitle {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.description {
  font-size: 1rem;
  line-height: 2.5;
}

.posts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.post {
  overflow: hidden;
}

.Button {
  display: flex;
  width: 100%;
  gap: 80%;
  align-items: flex-end;
  margin-bottom: 10px;
}

.post-header {
  display: flex;
  flex-direction: column;
  text-align: left;
  padding-top: 30px;
  padding-bottom: 10px;
}

.post-header a {
  display: flex;
  justify-content: flex-end;
}

.post-header h3 {
  color: #6f0ae2;
}

.post-category {
  font-size: 0.8rem;
  color: #888;
  margin-bottom: 5px;
}

.post-title {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.post-link {
  display: inline-block;
  padding: 5px 10px;
  color: #555;
  text-decoration: none;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.post-content {}

.post-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.post-author {
  display: flex;
  align-items: center;
}

.post-author-image {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 10px;
}

.post-author-info {
  margin-left: 10px;
}

.post-author-name {
  font-weight: bold;
  margin-bottom: 5px;
  font-size: 13px;
}

.post-author-date {
  color: #888;
  font-size: 0.8rem;
}

.view-all-posts {
  display: block;
  margin: 30px auto;
  padding: 10px 20px;
  background-color: #6f0ae2;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  border: none;
}

.view-all-posts:hover {
  background-color: #5200b1;
}

/* Responsive Design for Tablet and Smaller Screens */
@media (max-width: 768px) {
  .posts {
    grid-template-columns: 1fr;
  }

  .post-image {
    width: 100%;
    height: 150px;
  }

  .header {
    align-items: center;
  }

  .title {
    font-size: 0.9rem;
  }

  .subtitle {
    font-size: 2rem;
  }

  .description {
    font-size: 0.9rem;
  }

  .post-footer {
    flex-direction: column;
  }

  .post-author {
    flex-direction: column;
  }
}

/* Responsive Design for Mobile Screens */
@media (max-width: 480px) {
  .blogcontainer {
    padding: 10px;
  }

  .header {
    align-items: center;
  }

  .title {
    font-size: 0.8rem;
  }

  .subtitle {
    font-size: 1.5rem;
  }

  .description {
    font-size: 0.8rem;
  }

  .post-image {
    height: 100px;
  }

  .post-footer {
    flex-direction: column;
    align-items: flex-start;
  }

  .view-all-posts {
    font-size: 0.8rem;
    padding: 5px 10px;
  }
}

/* Freecontainer Section */
.freecontainer {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 150px;
  text-align: center;
}

h1 {
  font-size: 2.2em;
  margin-bottom: 10px;
}

/* Button Group */
.buttons {
  display: flex;
  border: 1px solid rgb(125, 125, 125);
  justify-content: center;
  gap: 20px;
  color: #555;
  background-color: white;
  border: none;
  border-radius: 5px;
}

.buttons > .btn:hover {
  background-color: rgb(211, 211, 211);
}

.mainbuttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding: 10px;
}

.startbutton > .btn {
  background-color: #700be3;
  color: white;
}

.startbutton > .btn:hover {
  background-color: #5907b7;
}

.btn {
  padding: 10px 20px;
  font-size: 1em;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

/* Additional Responsive Styles for Buttons */
@media (max-width: 768px) {
  h1 {
    font-size: 2em;
  }

  p {
    font-size: 1em;
  }

  .buttons {
    flex-direction: column;
  }

  .button {
    width: 100%;
  }
}
