/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Features Container */
.features-container {
  padding: 40px;
}

.features-header {
  text-align: center;
  margin-bottom: 50px;
}

.features-header h2 {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.features-header p {
  font-size: 1.2rem;
  color: #666;
  text-align: center;
}

/* Features Grid Layout */
.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-gap: 20px;
  display: inline-grid;
  grid-template-columns: auto auto auto;
}

/* Feature Box Styling */
.feature {
  padding: 50px;
  text-align: center;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(205, 205, 205, 0.1);

}

.feature-icon {
  font-size: 3rem;
  color: #007bff;
  margin-bottom: 20px;
}

.feature h3 {
  font-size: 1.28rem;
  font-weight: bold;
  margin-bottom: 12px;
}

.feature p {
  font-size: 1rem;
  color: #666;
}

/* Responsive Styles for Tablets */
@media (max-width: 768px) {
  .features-container {
    padding: 20px;
  }

  .features-header h2 {
    font-size: 2rem;
  }

  .features-header p {
    font-size: 1rem;
  }

  .features-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }

  .feature {
    padding: 30px;
  }

  .feature h3 {
    font-size: 1.2rem;
  }

  .feature p {
    font-size: 0.9rem;
  }
}

/* Responsive Styles for Mobile Screens */
@media (max-width: 480px) {
  .features-container {
    padding: 15px;
  }

  .features-header h2 {
    font-size: 1.8rem;
  }

  .features-header p {
    font-size: 0.9rem;
  }

  .features-grid {
    grid-template-columns: 1fr;
  }

  .feature {
    padding: 20px;
  }

  .feature h3 {
    font-size: 1.1rem;
  }

  .feature p {
    font-size: 0.85rem;
  }

  .feature-icon {
    font-size: 2.5rem;
  }
}
