/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 60px;
  font-family: sans-serif;
  background-color: rgb(252, 252, 252);
}

.testimonial {
  text-align: center;
  font-size: 40px;
  width: 70%;
  line-height: 1.4;
  margin-bottom: 40px;
}

.testmoniallogo {
  margin-bottom: 20px;
}

.author {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.author-image {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-right: 5px;
}

.author-info {
  text-align: center;
  margin-top: 5px;
}

.author-info h3 {
  margin-bottom: 5px;
}

.author-info p {
  font-size: 13px;
  color: #888;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .testimonial {
    font-size: 32px;
    width: 80%;
  }

  .author-image {
    width: 60px;
    height: 60px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 20px;
  }

  .testimonial {
    font-size: 24px;
    width: 90%;
  }

  .author-image {
    width: 55px;
    height: 55px;
  }
}

@media (max-width: 480px) {
  .testimonial {
    font-size: 20px;
    width: 100%;
  }

  .author-image {
    width: 50px;
    height: 50px;
  }

  .author-info p {
    font-size: 12px;
  }
}
